<template>
  <div :class="[ 'like', { 'like--active': isLike } ]" @click="toggle">
    <div class="like__wrapper">
      <component class="like__icon" :is="isLike ? 'iconLike' : 'iconUnlike'" />

      <div class="like__rays">
        <span class="like__ray like__ray--1"></span>
        <span class="like__ray like__ray--2"></span>
        <span class="like__ray like__ray--3"></span>
        <span class="like__ray like__ray--4"></span>
        <span class="like__ray like__ray--5"></span>
        <span class="like__ray like__ray--6"></span>
      </div>
    </div>

    <transition name="like" mode="out-in">
      <span class="like__amount" v-if="amount !== 0" :key="amount">{{ amount }}</span>
    </transition>
  </div>
</template>

<script>
import iconUnlike from 'mdi-vue/HeartOutline'
import iconLike from 'mdi-vue/Heart'
import { mapGetters } from 'vuex'

export default {
  name: 'c-like',

  components: {
    iconUnlike,
    iconLike
  },

  props: {
    id: {
      type: String,
      required: true
    },
    amount: {
      type: [String, Number],
      default: 0
    }
  },

  data: () => ({
    component: 'iconUnlike',
    isLike: false
  }),

  created () {
    this.isLike = ~this.getLikes.indexOf(this.id)
  },

  computed: {
    ...mapGetters({ getLikes: 'worklist/getLikes' })
  },

  methods: {
    toggle () {
      if (this.isLike) {
        this.$store.dispatch('worklist/delLike', this.id)
      } else {
        this.$store.dispatch('worklist/addLike', this.id)
      }

      this.isLike = !this.isLike
    }
  }
}
</script>

<style scoped>
  .like {
    user-select: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    min-width: 30px;
    box-sizing: border-box;
  }
  .like__wrapper {
    position: relative;
    padding: 5px;
    box-sizing: border-box;
  }
  .like__icon {
    display: block;
    width: 20px;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), calc(var(--ui-primary-l) + 15%));
    transition: color .2s;
  }
  .like:hover .like__icon,
  .like--active .like__icon {
    color: #d81b60;
  }
  .like__amount {
    margin-left: 5px;
    font-size: 14px;
  }

  .like__rays {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .like__ray {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 2px;
    height: 12px;
    opacity: 1;
    visibility: hidden;
    border-radius: 50%;
    background-image: linear-gradient(to bottom, #d81b60, transparent);
    transition: top .5s, left .5s, height .4s .2s, opacity .2s .5s;
  }
  .like__ray--1 {
    transform: translate(-50%, -50%) rotate(60deg);
  }
  .like__ray--2 {
    transform: translate(-50%, -50%) rotate(120deg);
  }
  .like__ray--3 {
    transform: translate(-50%, -50%) rotate(180deg);
  }
  .like__ray--4 {
    transform: translate(-50%, -50%) rotate(240deg);
  }
  .like__ray--5 {
    transform: translate(-50%, -50%) rotate(300deg);
  }
  .like__ray--6 {
    transform: translate(-50%, -50%);
  }

  .like--active .like__ray {
    height: 3px;
    opacity: 0;
    visibility: visible;
  }
  .like--active .like__ray--1 {
    top: 20%;
    left: 100%;
  }
  .like--active .like__ray--2 {
    top: 80%;
    left: 100%;
  }
  .like--active .like__ray--3 {
    top: 100%;
  }
  .like--active .like__ray--4 {
    top: 80%;
    left: 0;
  }
  .like--active .like__ray--5 {
    top: 20%;
    left: 0;
  }
  .like--active .like__ray--6 {
    top: 0;
  }
</style>